.Login {
  text-align: center;
}

.Login-logo {
  -webkit-animation: Login-logo-spin infinite 30s linear;
          animation: Login-logo-spin infinite 30s linear;
  height: 80px;
}

.Login-header {
  background-color: orange;
  height: 150px;
  padding: 20px;
  color: white;
}

.Login-title {
  font-size: 1.5em;
}

.Login-intro {
  font-size: large;
}

@-webkit-keyframes Login-logo-spin {
  from { -webkit-transform: rotate(3600deg); transform: rotate(3600deg); }
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}

@keyframes Login-logo-spin {
  from { -webkit-transform: rotate(3600deg); transform: rotate(3600deg); }
  to { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
}



